*
{
  margin: 0;
  padding: 0;
}
.App-input
{
  text-align: center;
  background-color: aqua;
  height: 300px;
  color: navy;
  font-size: 18px;
}
input
{
  font-size: 14px;
  border-radius: 5px;
  border: 1px solid yellow;
  outline: none;
  padding: 10px 5px;
  width: 200px;
}
.App-input button
{
  background-color: rgb(6, 248, 6);
  color: white;
  padding: 10px 20px;
  border-radius: 30px;
  margin-top: 10px;
  cursor: pointer;
  font-weight: bold;
  font-size: 17px;
}
.navActive
{
  color: rgb(6, 248, 6);
}
nav
{
  background-color: rgb(243, 9, 243);
  padding: 10px 0;
  display: flex;
  justify-content: center;
  margin-top: 5px;
 
}
nav ul
{
  display: flex;
  list-style-type: none;
  justify-content: space-between;
  align-items: center;
  width: 60%;
  color: white;
  font-weight: bold; 
}
nav ul li
{
  cursor: pointer;
  font-size: 17px;
  transition: all 0.5s ease-in-out;
}
nav ul li:hover
{
  transform: scale(1.4);
}
.App-container
{
  background-color: blue;
  color: white;
  padding: 10px;
}
